import { Link } from "react-router-dom";
import { useEffect } from "react";

const User = ({ getSingleUser, getRepos, user, match, repos }) => {
  useEffect(() => {
    getSingleUser(match.params.login);
    getRepos(match.params.login);
    // eslint-disable-next-line
  }, []);

  const {
    hireable,
    avatar_url,
    login,
    location,
    bio,
    html_url,
    blog,
    followers,
    following,
    public_repos,
    public_gists,
  } = user;
  return (
    <div className="col-12">
      <Link className="btn btn-dark text-light" to="/">
        Back to search
      </Link>
      <span className="ms-2">
        Hireable:
        {hireable ? (
          <i className="fa fa-times text-danger ms-1 "></i>
        ) : (
          <i className="fa fa-check text-success ms-1"></i>
        )}
      </span>
      {/* usercard */}
      <div className="card mt-2">
        <div className="row">
          <div className="col-6 text-center">
            <div>
              <img style={userStyle} src={avatar_url} alt={login} />
              {location && <h2 className="h4 ">{login}</h2>}
              {location && <h2 className="h4 p-3 bg-warning">{location}</h2>}
            </div>
          </div>
          <div className="col-6">
            <div className="py-3">
              <p>
                <strong>BIO :</strong>
                {bio}
              </p>
              <div className="my-3">
                <a className="btn btn-dark" href={html_url} target="_blanlk">
                  VISIT GITHUB
                </a>
              </div>
              <div>Login : {login}</div>
              <div className="mt-3">
                Website :
                {blog && (
                  <a href={blog} target="_blanlk">
                    {blog}
                  </a>
                )}
              </div>
            </div>
          </div>
        </div>
      </div>
      {/* infobar */}
      <div className="card mt-3">
        <div style={ifobar}>
          <button className="btn btn-danger mx-2">
            Followers : {followers}
          </button>
          <button className="btn btn-success mx-2">
            Following : {following}
          </button>
          <button className="btn btn-warning mx-2">
            Public Repos : {public_repos}
          </button>
          <button className="btn btn-info mx-2">
            Public Gists : {public_gists}
          </button>
        </div>
      </div>
      {/* repos */}
      <ul className="list-group">
        {repos.map((repos) => (
          <li className="list-group-item" key={repos.id}>
            <a href={repos.html_url}>{repos.name}</a>
          </li>
        ))}
      </ul>
    </div>
  );
};

const userStyle = {
  width: "110px",
  height: "110px",
  borderRadius: "50%",
  margin: "30px auto",
  display: "block",
};
const ifobar = {
  display: "flex",
  justifyContent: "center",
  padding: "15px 0",
};
export default User;
