import { useState } from "react";

const Search = ({ searchUsers, users, clearUsers }) => {
  const [text, setText] = useState("");

  const submitHandler = (e) => {
    e.preventDefault();
    if (text.trim() !== "") {
      searchUsers(text);
      setText("");
    }
  };

  const onChangeHandler = (e) => setText(e.target.value);

  return (
    <form onSubmit={submitHandler}>
      <div className="mb-3">
        <input
          className="form-control"
          placeholder="Enter name"
          value={text}
          onChange={onChangeHandler}
          name="text"
          autoComplete="off"
        />
      </div>
      <button className="btn btn-primary d-block mt-3 mb-3 w-100 text-white">
        Search
      </button>
      {users.length > 0 && (
        <button
          onClick={clearUsers}
          type="button"
          className="btn btn-secondary 
         d-block mt-3 mb-3 w-10 text-white"
        >
          Clear
        </button>
      )}
    </form>
  );
};

export default Search;
